.chat-wrapper {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 440px;
    background: white;
    border-top: 1px solid #4f4c4c;;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
    transform: translateY(100%);
    transition: transform 0.5s ease;
    display: flex;
    flex-direction: column;
    z-index: 100;
}

.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    font-size: 18px;
      font-size: 0.875rem; /* Smaller font size */
      background-color: #242424;;
      color: #fff;
}

.close-button {
    background: transparent;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
}

.chat-body {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
    background: #242424;
}

.chat-message {
    margin-bottom: 10px;
    padding: 8px 12px;
    border-radius: 4px;
    max-width: 900px;
    margin: auto ;
}

.chat-message.user {
    color: white;
    align-self: flex-end;
      font-size: 0.875rem; /* Smaller font size */
      color: #fff;

}

.chat-message.bot {
    align-self: flex-start;
      font-size: 0.875rem; /* Smaller font size */
      color: #fff;
      text-align: left;
}

.chat-input {
    display: flex;
    padding: 10px;
    border-top: 1px solid #18181882;
    background: white;
      font-size: 0.875rem; /* Smaller font size */
      background-color: #444444;
      color: #fff;
      place-content: center;
}

.chat-input input {
    flex: 1;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
      font-size: 0.875rem; /* Smaller font size */
      background-color: #444444;
      color: #fff;
      max-width: 990px;
}

.chat-input button {
    margin-left: 10px;
    padding: 8px 16px;
    background: #0078d4;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  padding: 0.5rem 1rem; /* Adjusted padding */
  font-size: 0.875rem; /* Smaller font size */
  background-color: #444444;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 16px;
}
