/* Reset default margins and paddings */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Font and smoothing */
html {
  font-size: 14px; /* Smaller base font size */

    overflow-y: scroll; /* Ensure the vertical scrollbar is always visible */
    overflow-x: hidden; /* Prevent horizontal scrolling */
}
html {
    scroll-behavior: smooth;
}

body {
  font-family: Monospace, 'Open Sans', sans-serif; /* Minimalistic font */
  font-size: 0.875rem; /* Smaller font size */
  line-height: 1.6;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
  -webkit-font-smoothing: subpixel-antialiased; /* More blurry text */
  -moz-osx-font-smoothing: grayscale; /* More blurry text */
  transition: background-color 0.3s ease, color 0.3s ease;
}

