/* Custom Scrollbar for WebKit Browsers */
*::-webkit-scrollbar {
  width: 10px; /* Vertical scrollbar width */
  height: 10px; /* Horizontal scrollbar height */
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent thumb */
  border-radius: 5px; /* Rounded corners for the thumb */
  background-clip: padding-box; /* Prevents border overlap */
  border: none; /* No border around the thumb */
  transition: background-color 0.3s ease; /* Smooth color transition */
}

*::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.7); /* Darker thumb on hover */
}

*::-webkit-scrollbar-track {
  background: transparent; /* Fully transparent track */
}

*::-webkit-scrollbar-track.dark-mode {
  background: transparent; /* Dark mode track remains transparent */
}

/* Firefox Custom Scrollbar */
* {
  scrollbar-width: thin; /* Thin scrollbar */
  scrollbar-color: rgba(0, 0, 0, 0.5) transparent; /* Semi-transparent thumb, fully transparent track */
}

body.dark-mode * {
  scrollbar-color: rgba(0, 0, 0, 0.7) transparent; /* Darker thumb for dark mode, track stays transparent */
}

*::-webkit-scrollbar-button {
  background: transparent; /* Makes scrollbar arrows transparent if display:none doesn't work */
  width: 0;
  height: 0;
}

*::-webkit-scrollbar-button {
  display: none; /* Hides the scrollbar arrows */
}

/* Hide Scrollbar Buttons Completely */
*::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  background: none; /* Remove background graphic */
  border: none; /* Remove any borders */
  display: none; /* Ensure buttons are not rendered */
}


