/* Container for center alignment */
.container {
  text-align: center;
  margin-top: 50px;
}

/* Animated element styles */
.animated-element {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
  transition: transform 0.25s ease;
  /* Initially no animation */
  animation: none;
}

/* Keyframes for bouncing animation */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

/* Keyframes for rotating animation */
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Add animation classes for bounce and rotate when isAnimating is true */
.animate {
  animation: bounce 1s infinite, rotate 2s infinite linear;
  animation-duration: var(--animation-speed);
}

/* Shake animation keyframes */
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-5px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(5px);
  }
}

/* Shake animation class */
.shake {
  animation: shake 0.15s ease-in-out;
}

@keyframes shake2 {
  0%, 100% {
    transform: translateX(0);
  }
  7%, 28%, 47%, 72%, 85% {
    transform: translateX(-5px);
  }
  12%, 33%, 55%, 78% {
    transform: translateX(5px);
  }
}

/* Shake animation class */
.shake2 {
  animation: shake2 0.15s ease-in-out;
}

